import React from 'react'
import RecipeCard from '../RecipeCard/RecipeCard';
import RecipesService from '../../services/recipes.service'
import './MyRecipes.css'
import { withAuth } from '../../context/auth.context';

class MyRecipes extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            recipes: []
        }
        this.recipesService = new RecipesService();
    }

    refreshState(){
        this.recipesService.get()
        .then(res => {
            this.setState({ recipes: res.data })
        })
        .catch(err => console.error(err))
    }

    componentDidMount(){
        this.refreshState();
    }

    displayRecipes(){
        return this.state.recipes.map((recipe) => {
            return (
                <RecipeCard id={recipe.id} dishName={recipe.dishName} cousine={recipe.cousine} type={recipe.type} image={recipe.image} prepTime={recipe.prepTime} refreshState={() => this.refreshState()} key={recipe.id} />
            )
        })
    }

    handleLogout = () => {
        this.props.logout();
    }
    
    render(){
        return (
            <div className="recipes-container">
                {this.state.recipes.length === 0 ? <h2>Lets get dirt, start to cook and create your first Recipe</h2> :
                    this.displayRecipes()
                }
            </div>
        )
    }
}

export default withAuth(MyRecipes);