import React from 'react'
import { Route, Switch } from 'react-router';
import AnonRoute from './components/AnounRoute/AnounRoute';
import PrivateRoute from './components/PrivateRoute/PrivateRoute';
import CreateRecipe from './pages/CreateRecipe/CreateRecipe';
import EditRecipe from './pages/EditRecipe/EditRecipe';
import EditUser from './pages/EditUser/EditUser';
import Home from './pages/Home/Home';
import Recipes from './pages/Recipes/Recipes';
import Recipe from './pages/Recipe/Recipe';
import Signup from './pages/Signup/Signup';
import { withAuth } from './context/auth.context';
import { NavLink } from 'react-router-dom';
import './App.css'

class App extends React.Component {

  state = {
    user: null
  }

  componentDidMount() {
    this.setState({ user: this.props.user})
  }

  render() {

    return (
      <div className="app">
        <nav className="navbar">
          <h2 className="nav-title">YOUR OWN RECIPES APP</h2>
          {this.props.user ? <button className="nav-logout-bt"onClick={() => this.props.logout()}><NavLink exact to="/">Logout</NavLink></button> : null}
          {this.props.user ? <button className="nav-edit-user-bt"><NavLink exact to="/edit-user">Edit User</NavLink></button> : null}
        </nav>

        <Switch>
          <PrivateRoute path="/create-recipe" component={CreateRecipe} />
          <PrivateRoute exact path="/edit-recipe/:id" component={EditRecipe} />
          <PrivateRoute exact path="/edit-user/:id" exact component={EditUser} />
          <AnonRoute exact path="/" component={Home} />
          <PrivateRoute exact path="/recipes" component={Recipes} />
          <PrivateRoute exact path="/recipes/:id" component={Recipe} />
          <AnonRoute exact path="/signup" exact component={Signup} />
          <Route path="*" render={() => 
            <div>
              <p2>404 NOT FOUND</p2>
              <img src="./resources/img/broaken-plate.jpg" alt="Broaken Plate"></img>
            </div>} />
        </Switch>
      </div>
    );
  }
}

export default withAuth(App);
