import React, { Component } from 'react';
import { withAuth } from '../../context/auth.context';
import { Link } from 'react-router-dom';
const EMAIL_PATTERN = /(?:[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*|"(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21\x23-\x5b\x5d-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])*")@(?:(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?|\[(?:(?:(2(5[0-5]|[0-4][0-9])|1[0-9][0-9]|[1-9]?[0-9]))\.){3}(?:(2(5[0-5]|[0-4][0-9])|1[0-9][0-9]|[1-9]?[0-9])|[a-z0-9-]*[a-z0-9]:(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21-\x5a\x53-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])+)\])/ 

const validators = {
    username: (value) => {
        let message;
        if(!value){
            message = 'Username is required';
            }
        return message;
    },

    email: (value) => {
        let message;
        if(!value){
            message = 'Email is required';
            } else if(!EMAIL_PATTERN.test(value)){
            message = 'Invalid email';
            }
        return message;
    },

    password: (value) => {
        let message;
        if(!value){
            message = 'Password is required';
            } else if(value.length < 3){
            message = 'Invalid password'
            }
        return message;
    },
}

class Signup extends Component {
    constructor(props){
        super(props);
        this.state = {
        fields: {
            username: "",
            email: "",
            password: ""
        },
        errors: {
            username: null,
            email: null,
            password: null
        }
        }
    }

    handleSubmit(event){
        event.preventDefault();
        if(this.isValid()){
            this.props.signup(this.state.fields);
        }
    }

    handleChange(event){
        const { name, value } = event.target;
        this.setState({
        fields: {
            ...this.state.fields,
            [name]: value
        },
        errors: {
            ...this.state.errors,
            [name]: validators[name](value)
        }
        })
    }

    isValid() {
        const { errors } = this.state;
        return !Object.keys(errors).some(key => errors[key]);
    }

    render() {
        const { fields, errors } = this.state;
        return (
            <div className="home-box">
            <form onSubmit={(event) => this.handleSubmit(event)} >
                <div className="form-item">
                    <label htmlFor="username">Username: </label>
                    <input className={`${errors.username ? "error-input" : ""}`} type="text" name="username" value={fields.username} onChange={(event) => this.handleChange(event)} />
                    {errors.username && <p style={{ color: 'red' }}>{errors.username}</p>}
                </div>
                <div className="form-item">
                    <label htmlFor="email">Email: </label>
                    <input className={`${errors.email ? "error-input" : ""}`} type="text" name="email" value={fields.email} onChange={(event) => this.handleChange(event)} />
                    {errors.email && <p style={{ color: 'red' }}>{errors.email}</p>}
                </div>
                <div className="form-item">
                    <label htmlFor="password">Password: </label>
                    <input className={`${errors.password ? "error-input" : ""}`} type="password" name="password" value={fields.password} onChange={(event) => this.handleChange(event)} />
                    {errors.password && <p style={{ color: 'red' }}>{errors.password}</p>}
                </div>
                <button disabled={!this.isValid()} type="submit">Create Chef</button>
            </form>
            <Link to="/">Login</Link>
        </div>
        )
    }
}

export default withAuth(Signup);