import React from 'react';
import './RecipeCard.css'
import { Link } from 'react-router-dom';
import RecipesService from '../../services/recipes.service';
import { withAuth } from '../../context/auth.context';

function RecipeCard ({ id, dishName, cousine, type, image, ingredients, prepTime, preparation, howToCook, refreshState}) {
  
  const recipesService = new RecipesService();

  const deleteRecipe = () => {
    
    console.log("deleting", id)


    recipesService.deleteOne(id)
    .then(() => {
      console.log('deleted')
      refreshState();
    })
    .catch((err) => console.error(err))
  }

  return(
    <div className="card">
    <div className="img-container">
      <Link to={`/recipes/${id}`}>
        <img class="img-to-fit"src={image} alt={dishName} />
      </Link>
    </div>
    <div className="card-body">
      <h3 className="card-title">{dishName}</h3>
      <div className="card-text-container">
        <p className="card-text">{cousine}</p>
        <p className="card-text">{type}</p>
        <p className="card-text">{prepTime}</p>
      </div>
        <p className="card-text">{ingredients && ingredients}</p>
        <p className="card-text">{preparation && preparation}</p>
        <p className="card-text">{howToCook && howToCook}</p>
      <div className="buttons-recipe-card">
        {<Link to={`/edit-recipe/${id}`}>
          <button className="corner-left">Edit</button>
        </Link>}
        <button className="corner-right" onClick={() => deleteRecipe()}>Delete</button>
      </div>
    </div>
  </div>
  )
}

export default withAuth(RecipeCard);