//LOGIN

import React, { Component } from 'react';
import { withAuth } from '../../context/auth.context';
import { Link } from 'react-router-dom';
import './Home.css';

const validators = {
  username: (value) => {
    let message;
    if(!value){
        message = 'Username is required';
        }
        return message;
    },

  password: (value) => {
    let message;
    if(!value){
        message = 'Password is required';
        } else if(value.length < 5){
        message = 'Invalid password'
        }
        return message;
    },
}

class Login extends Component {
    constructor(props){
    super(props);
        this.state = {
            fields: {
                username: "",
                password: ""
            },
            errors: {
                username: null,
                password: null
            }
        }
    }

    handleSubmit(event){
        event.preventDefault();
        if(this.isValid()){
            console.log(this.state.fields);
            this.props.login(this.state.fields);
        }
    }

    handleChange(event){
        const { name, value } = event.target;
        this.setState({
        fields: {
            ...this.state.fields,
            [name]: value
        },
        errors: {
            ...this.state.errors,
            [name]: validators[name](value)
        }
        })
    }

    isValid() {
        const { errors } = this.state;
        return !Object.keys(errors).some(key => errors[key]);
    }

    render() {
        const { fields } = this.state;
        return (
            <div className="home-box">
                <form className="form-box" onSubmit={(e) => this.handleSubmit(e)}>
                    <input className="login-field" type="text" name="username" value={fields.username} onChange={(e) => this.handleChange(e)} placeholder="Username:"/>
                    <input className="login-field" type="password" name="password" value={fields.password} onChange={(e) => this.handleChange(e)} placeholder="Password:"/>
                    <button className="login-field" disabled={!this.isValid() || !fields.username || !fields.password} type="submit">Login</button>
                </form>
                <Link to="/signup">
                    <button className="signup-bt" >Signup</button>
                </Link>
            </div>
        )
    }
}

export default withAuth(Login);